import { _decorator, Component, Node, log, Prefab, sys } from 'cc';
import VDBaseScreen from '../../../../vd-framework/ui/VDBaseScreen';
import VDScreenManager from '../../../../vd-framework/ui/VDScreenManager';
import { ex_Director } from '../common/ex_Director';
import { ex_LevelComparison } from './ex_LevelComparison';
const { ccclass, property } = _decorator;

@ccclass('ex_LoadingScreen')
export class ex_LoadingScreen extends Component {
    start() {
        VDScreenManager.instance.assetBundle.load('res/prefabs/popup/popup_notify',
        (err, data) => {
            if (!err) {
                this._loadAsset(0);
            }
            else {
                log("load error  " + err + " _loadAsset");
                if (sys.isBrowser) {
                    alert("Không có kết nối, vui lòng thử lại");
                }
            }
        });
    }
    _loadAsset(){

    }
    onClickBtnLevelComparison() {
        log(`onClickBtn2MainGame`);
        VDScreenManager.instance.assetBundle.load("res/prefabs/screen/level_comparison_screen", Prefab, (error, prefab) => {
            if (error) {
                log(`bundle.load: ${error}`);
            }
            else {
                log("load loading sucess")
                // VDScreenManager.instance.initWithRootScreen(prefab);
                VDScreenManager.instance.initWithRootScreen(prefab, (screen) => {
                    log('initWithRootScreen ' + screen.name + ' success!');
                });
            }
        })

        // let level_screen = VDScreenManager.instance.assetBundle.get('res/prefabs/screen/level_comparison_screen', Prefab)!;
        // VDScreenManager.instance.pushScreen(level_screen, (screen: VDBaseScreen) => { }, true);
        
        let play_screen = VDScreenManager.instance.assetBundle.get('res/prefabs/screen/level_comparison_screen', Prefab)!;
        VDScreenManager.instance.replaceScreenAtIndex(play_screen, 0, (screen: VDBaseScreen) => {
            // ex_Director.instance.playScreen = screen as ;
        });


    }
    update(deltaTime: number) {
        
    }
}


